import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import {
    initTranslations,
    getTranslation,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import {
    resetAuthorization,
    initializeAppConfig,
} from '@Green-Dot-Corporation/web-app-utils-api-lib';
import {
    LoadingFlow,
    EurekaErrorBoundary,
} from '@Green-Dot-Corporation/eureka-ui-loading';
import {
    initializeImages,
    getImagePathForKey,
} from '@Green-Dot-Corporation/eureka-ui-media';
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import Landing from './components/landing/Landing';
import Bump from './components/bump/Bump';
import BumpAttemptDeclined from './components/bump/BumpAttemptDeclined';
// Services
import { getDDFeatureConfig } from './services/DDSwitchService';
// Utils
import { importCSS } from './utils/CSSImport';
import {
    cleanData as cleanGatewayHttpClientData,
    setApplicationId,
    setEnv,
    setPrefixAPIUrl,
} from './utils/gatewayHttpClient';
import { setPlatform, setProgramCode } from './utils/urlHelper';
// Constants
import GlobalSettings from './utils/GlobalSettings';
import { BRAND, PLATFORM, ENVIRONMENT, PROGRAM_CODE } from './const/urlConst';
import { ROUTES_CONSTS } from './const/routesConst';
import imageLibrary, { IMAGE_KEYS } from './media/images/imageLibrary';
// Styles
import './styles/dd-switch-module.scss';

const REFRESH_TOKEN_INTERVAL_SECONDS = 9.5 * 60;

class DDSwitchModule extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        accountIdentifier: PropTypes.string,
        accessToken: PropTypes.string,
        brand: PropTypes.string,
        applicationId: PropTypes.string,
        env: PropTypes.string,
        onRefreshToken: PropTypes.func,
        platform: PropTypes.string,
        // String containing the CSS selector for rendering Transact inside an iframe
        transactContainer: PropTypes.string,
    };

    static defaultProps = {
        platform: PLATFORM.BAAS,
        brand: BRAND.GO2BANK,
        env: ENVIRONMENT.PROD,
    };

    state = {
        isLoading: true,
        hasError: false,
        isAutoSwitchEnabled: false,
    };

    render() {
        const { isLoading, hasError } = this.state;
        const { transactContainer } = this.props;

        return (
            <div id="root">
                <BrowserRouter>
                    <EurekaErrorBoundary
                        calloutConfig={{
                            title: getTranslation(
                                'errorBoundary.default.title',
                            ),
                            msg: getTranslation('errorBoundary.default.msg'),
                            buttonText: getTranslation(
                                'errorBoundary.default.button',
                            ),
                            contextCls: `${this.baseCls}__error-boundary`,
                            isPageHeading: false,
                            illustrationSrc: getImagePathForKey(
                                IMAGE_KEYS.WARNING_ILLUSTRATION,
                            ),
                        }}
                        modalConfig={{
                            dialogContextCls: `${this.baseCls}__error-boundary-modal`,
                        }}
                        redirectURL={ROUTES_CONSTS.LANDING}
                    >
                        <LoadingFlow
                            contextCls={`${this.baseCls}__loading`}
                            loadingStateLocale={{
                                title: getTranslation('loadingState.title'),
                            }}
                            isLoading={isLoading}
                            hasError={hasError}
                        >
                            <Routes>
                                <Route
                                    path="/*"
                                    element={this.renderLandingPage()}
                                />
                                <Route
                                    path={ROUTES_CONSTS.LANDING}
                                    element={this.renderLandingPage()}
                                />
                                <Route
                                    path={ROUTES_CONSTS.BUMP}
                                    element={
                                        <Bump container={transactContainer} />
                                    }
                                />
                                <Route
                                    path={ROUTES_CONSTS.BUMP_ATTEMPT_DECLINED}
                                    element={<BumpAttemptDeclined />}
                                />
                            </Routes>
                        </LoadingFlow>
                    </EurekaErrorBoundary>
                </BrowserRouter>
            </div>
        );
    }

    componentDidMount() {
        this.initialSetup();
        this.fetchConfig();
        this.applyDesignToken();
        this.initializeImageLibrary();
        this.refreshTokenTimer = this.initialRefreshTokenTimer();
    }

    componentWillUnmount() {
        cleanGatewayHttpClientData();
        this.clearRefreshTokenTimer();
    }

    renderLandingPage = () => {
        const { ...rest } = this.props;
        const { isAutoSwitchEnabled } = this.state;

        return <Landing {...rest} isAutoSwitchEnabled={isAutoSwitchEnabled} />;
    };

    fetchConfig = async () => {
        await this.initI18nConfig();
        const newState = { isLoading: false };

        try {
            const { isAutoSwitchEnabled } = await getDDFeatureConfig();

            newState.isAutoSwitchEnabled = isAutoSwitchEnabled;

            if (isAutoSwitchEnabled) {
                newState.shouldShowLandingPage = true;
            }
        } catch (e) {
            newState.hasError = true;
        } finally {
            this.setState(newState);
        }
    };

    initialSetup = () => {
        const { accessToken, applicationId, platform, env, brand } = this.props;

        if (accessToken) {
            resetAuthorization(accessToken);
        }

        if (brand) {
            GlobalSettings.Brand = brand;
        }

        const upperCaseBrand = GlobalSettings.Brand.toUpperCase();
        const programCode = PROGRAM_CODE[upperCaseBrand];
        setProgramCode(programCode);

        if (applicationId) {
            setApplicationId(applicationId);
            initializeAppConfig({ applicationId, programCode });
        }

        if (platform) {
            setPlatform(platform.toUpperCase());
        }

        if (env) {
            setEnv(env.toUpperCase());
            GlobalSettings.Environment = env.toUpperCase();
            setPrefixAPIUrl();
        }
    };

    initI18nConfig = async () => {
        const initTranslationsOption = {
            defaultTranslations: {
                'en-US': () => import('./i18n/en-us.json'),
            },
            shouldLoadStrings: false,
        };
        let i18nResult = await initTranslations(initTranslationsOption);
        // Add this logic to avoid the i18n false positive return. The initTranslations will return promise successfully even the i18n is not ready.
        // However in this case, the i18n name will be 'bound bound value' to indicate the load has not been ready. So in this case, we need to re-init i18n

        if (i18nResult?.name === 'bound bound value') {
            i18nResult = await initTranslations(initTranslationsOption);
        }
    };

    applyDesignToken = async () => {
        const { brand } = this.props;
        await importCSS(brand);
    };

    initializeImageLibrary = async () => {
        await initializeImages({
            defaultImages: imageLibrary,
        });
    };

    initialRefreshTokenTimer = () => {
        this.clearRefreshTokenTimer();
        const { onRefreshToken } = this.props;

        if (!onRefreshToken) {
            return undefined;
        }
        // access token has 10 minutes expiration time
        // set 9.5 min interval threshold to refresh token before token expired
        const intervalThresholdMS = REFRESH_TOKEN_INTERVAL_SECONDS * 1000;
        const timer = setInterval(async () => {
            const refreshedToken = await onRefreshToken();

            if (refreshedToken) {
                resetAuthorization(refreshedToken);
            }
        }, intervalThresholdMS);

        return () => {
            clearInterval(timer);
        };
    };

    clearRefreshTokenTimer = () => {
        if (this.refreshTokenTimer) {
            this.refreshTokenTimer();
        }
    };

    baseCls = 'dd-switch-module';
}

export default DDSwitchModule;
